import { createSlice } from "@reduxjs/toolkit";
import { RootState } from "./store";

interface AppState {
  lang: string;
}

function getInitialState(): AppState {
  const lang = localStorage.getItem("lang") || "zh_CN";

  return {
    lang,
  };
}

// 1. redux
let initialState: AppState = getInitialState();

/*
  const initialState: Xxx = {
    xxx: xxx,
    yyy: xxx
  }
*/

// 2. redux
export const appSlice = createSlice({
  // redux
  name: "app",
  // 
  initialState,
  // reducer --> action
  reducers: {
    // reducer
    // statestateprevState
    // actiondispatchaction {type: xxx, payload: }
    setLang(state, action) {
      const lang = action.payload;
      localStorage.setItem("lang", lang);
      // newState
      state.lang = lang;
    },
  },
});

//   
// , langconst lang = useAppSelector(selectLang)
export const selectLang = (state: RootState) => state.app.lang;

// actionactionreducer
// lang
// const dispatch = useAppDispatch()
// dispatch(setLang())  payload
export const { setLang } = appSlice.actions;

// 3. reduxreducerstore
// /
export default appSlice.reducer;
