import {configureStore} from "@reduxjs/toolkit";

// reduxreducer
import appSlice from "./appSlice";
import userSlice from "@/pages/login/slice";

// 
// reduxstore
// TODOreduxreducer/redux
export const store = configureStore({
  reducer: {
    app: appSlice,
    user: userSlice,
  },
});

